import { toRefs, reactive } from 'vue';
import { useRouter } from 'vue-router';
export default {
  name: 'liaotianheader',
  props: {
    HomeContent: Array
  },
  setup(props, context) {
    const router = useRouter();
    const data = reactive({
      tubiaodesc: [{
        id: 'B001',
        icon: 'phone',
        name: '电话号',
        color: '#bbb'
      }, {
        id: 'B002',
        icon: 'wechat',
        name: '微信号',
        color: '#bbb'
      }, {
        id: 'B003',
        icon: 'graphic',
        name: '发简历',
        color: '#bbb'
      }, {
        id: 'B004',
        icon: 'clear',
        name: '不感兴趣',
        color: '#f00'
      }],
      HomeContent: props.HomeContent,
      showjubao: true
    });
    const toup = () => {
      router.go(-1);
    };
    const closeclick = () => {
      data.showjubao = false;
    };
    return {
      ...toRefs(data),
      toup,
      closeclick
    };
  }
};