import myEventBus from '@/assets/js/bus';
import { ref, onMounted, nextTick } from 'vue';
import { getdata } from '@/utils/request';
export default {
  name: 'liaotianfooter',
  setup() {
    const inptval = ref('');
    const showbot = ref(false);
    const faceList = ref([]);
    const inpt = ref(null);
    const emitter = myEventBus();
    const getdatas = () => {
      getdata('/emoji.json').then(res => {
        faceList.value = res.splice(0, 100);
      });
    };
    const pushinptval = () => {
      emitter.emit('pushval', inptval.value);
      inptval.value = '';
      showbot.value = false;
    };
    onMounted(() => {
      getdatas();
      emitter.on('closebqf', () => {
        if (showbot.value === true) {
          showbot.value = false;
        }
      });
    });
    const getfaceList = i => {
      inptval.value = inptval.value + faceList.value[i].char;
      nextTick(() => {
        inpt.value.focus();
      });
    };
    const showbiaoqingfu = () => {
      showbot.value = !showbot.value;
    };
    return {
      inptval,
      showbot,
      faceList,
      getdatas,
      pushinptval,
      getfaceList,
      showbiaoqingfu,
      inpt
    };
  }
};